<template>

  <div>
    <van-nav-bar
  title="标题"
  left-text="返回"
  right-text="按钮"
  left-arrow
  @click-left="onClickLeft"
  @click-right="onClickRight"
/>
    <div>注:该页面还需后面再优化,先把数据摆上来
    <br>
    总计{{data.length}}个单词

    <van-sidebar
      v-model="activeKey">
      <van-sidebar-item
        title="标签名称" />
      <van-sidebar-item
        title="标签名称" />
      <van-sidebar-item
        title="标签名称" />
    </van-sidebar>
    <van-grid>
      <van-grid-item
        text="全部单词" />
      <van-grid-item
        text="被标记的单词" @click="markedWords"/>
      <van-grid-item
        text="超过30天的单词" @click="allFinishedWords" />
      <van-grid-item
        text="超过30天且被标记的单词" @click="finishedMarkedWords" />
        <van-grid-item
        text="随机100个单词" @click="randomWords" />
        <van-grid-item
        text="随机100个超过30天的单词" @click="randomFinishedWords" />

    </van-grid></div>

  </div>
</template>

<script>
import axios from 'axios'
import {Toast} from 'vant'
export default {
  name: 'WordsIndex',
  data() {
    return {
      activeKey: 0,
      data: {},
    }
  },
  methods: {
    onClickLeft() {
      Toast('返回');
      history.go(-1)
    },
    onClickRight() {
      Toast('按钮');
    },
    randomWords(){
      axios({
        method: 'post',
        url: this.$baseURL+'/word/randomWords',
        data: {
          uid: this.$store.state.uid,
        },
      }).then((res) => {
        this.$router.push({
          path: '/phone/reciteWords',
          query: { data: res.data.data },
        })
        if (
          res.data.data.length == 0 &&
          !(
            this.$store.state.uid == null ||
            this.$store.state.uid == '' ||
            this.$store.state.uid === undefined
          )
        ) {
          alert('无数据,请先去添加')
        }
      })

    },
    randomFinishedWords(){
axios({
        method: 'post',
        url: this.$baseURL+'/word/randomFinishedWords',
        data: {
          uid: this.$store.state.uid,
        },
      }).then((res) => {
        this.$router.push({
          path: '/phone/reciteWords',
          query: { data: res.data.data },
        })
        if (
          res.data.data.length == 0 &&
          !(
            this.$store.state.uid == null ||
            this.$store.state.uid == '' ||
            this.$store.state.uid === undefined
          )
        ) {
          alert('无数据,请先去添加')
        }
      })

    },
    allFinishedWords() {
      axios({
        method: 'post',
        url: this.$baseURL+'/word/allFinishedWords',
        data: {
          uid: this.$store.state.uid,
        },
      }).then((res) => {
        this.$router.push({
          path: '/phone/reciteWords',
          query: { data: res.data.data },
        })
        if (
          res.data.data.length == 0 &&
          !(
            this.$store.state.uid == null ||
            this.$store.state.uid == '' ||
            this.$store.state.uid === undefined
          )
        ) {
          alert('无数据,请先去添加')
        }
      })
    },
    /**
     * 超过30天且被标记的数据
     */
    finishedMarkedWords() {
      axios({
        method: 'post',
        url: this.$baseURL+'/word/finishedMarkedWords',
        data: {
          uid: this.$store.state.uid,
        },
      }).then((res) => {
        this.$router.push({
          path: '/phone/reciteWords',
          query: { data: res.data.data },
        })
        if (
          res.data.data.length == 0 &&
          !(
            this.$store.state.uid == null ||
            this.$store.state.uid == '' ||
            this.$store.state.uid === undefined
          )
        ) {
          alert('无数据,请先去添加')
        }
      })
    },
    markedWords() {
      axios({
        method: 'post',
        url: this.$baseURL+'/word/markedWords',
        data: {
          uid: this.$store.state.uid,
        },
      }).then((res) => {
        this.$router.push({
          path: '/phone/reciteWords',
          query: { data: res.data.data },
        })
        if (
          res.data.data.length == 0 &&
          !(
            this.$store.state.uid == null ||
            this.$store.state.uid == '' ||
            this.$store.state.uid === undefined
          )
        ) {
          alert('无数据,请先去添加')
        }
      })
    },
  },
  mounted() {
    if (
      this.$store.state.uid == null ||
      this.$store.state.uid == '' ||
      this.$store.state.uid === undefined
    ) {
      alert('请先登录')
      this.$router.push('/phone/login')
    }
    axios({
      method: 'post',
      url: this.$baseURL+'/word/allWords',
      data: {
        uid: this.$store.state.uid,
      },
    }).then((res) => {
      this.data = res.data.data
      console.log(this.data)
      this.getYears()
      if (
        this.data.length == 0 &&
        !(
          this.$store.state.uid == null ||
          this.$store.state.uid == '' ||
          this.$store.state.uid === undefined
        )
      ) {
        alert('还没有添加单词哦,先去添加单词吧')
      }
    })
  },
}
</script>
